* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country__img {
  width: 30px;
  height: 20px;
  margin-right: 5px;
}

.country-item {
  display: flex;
  align-items: center;
}
.country__item-big {
  list-style: none;
}
.country__wrap-head {
  display: flex;
  align-items: center;
}
.country__title-big {
  font-size: 28px;
  font-weight: 700;
}

.country__title-small {
  font-size: 18px;
  font-weight: 600;
}

.country__title-text {
  font-weight: 700;
}
.country__value-text {
  font-weight: 400;
}
